  (0, _qunit.test)('Incident category management', function (assert) {
    runWithPouchDump('incident', function () {
      authenticateUser();
      visit('/admin/inc-category');
      andThen(function () {
        assert.equal(currentURL(), '/admin/inc-category', 'Incident Categories url is correct');
        click('button:contains(+ new category)');
      });
      andThen(function () {
        assert.equal(currentURL(), '/admin/inc-category/edit/new', 'New incident category URL is correct');
        fillIn('.incident-category-name input', 'Infection Control');
        addItem(assert, 'Surgical Site Infection');
      });
      andThen(function () {
        addItem(assert, 'Hospital Acquired Infection');
      });
      andThen(function () {
        click('button:contains(Delete)');
        waitToAppear('.modal-dialog');
      });
      andThen(function () {
        assert.equal(find('.modal-title').text(), 'Delete Item', 'Delete Item modal appears');
        click('.modal-footer button:contains(Ok)');
        waitToDisappear('.modal-dialog');
      });
      andThen(function () {
        assert.equal(find('.incident-category-item:contains(Surgical Site Infection)').length, 0, 'Deleted incident category item disappears');
        click('.panel-footer button:contains(Update)');
        waitToAppear('.modal-dialog');
      });
      andThen(function () {
        assert.equal(find('.modal-title').text(), 'Incident Category Saved', 'Incident Category saved modal appears');
        click('button:contains(Return)');
      });
      andThen(function () {
        assert.equal(currentURL(), '/admin/inc-category', 'Incident Categories url is correct');
        assert.equal(find('td.incident-catergory-name:contains(Infection Control)').length, 1, 'New incident category displays in listing');
      });
    });
  });

